.wrapList {
    display: flex;
    flex-direction: column;
    gap: 64px;
    align-items: center;
}

.list {
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    gap: 32px;
    justify-content: center;

}

.teacherText {
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    line-height: 1.25;
    margin-top: 20px;
    color: #121417;
}

.buttonMore {
    border-radius: 12px;
    padding: 16px 48px;
    width: 183px;
    height: 60px;
    background: #f4c550;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.55556;
    color: #121417;
    margin-bottom: 40px;

    &:hover {
        background-color: #FBE9BA;
    }
}